.boxnewuser{
	background-color: #fff;
	box-shadow:5px 5px 10px 2px rgba(23, 12, 237, 0.2);
	border-radius: 10px;
	border:solid 1px rgb(113, 149, 188);
    width: 60%;
	min-width: 450px;
}
.boxhedernewuser{
	width: 100%;
	height: 80px;
	background-color: rgb(93, 93, 236);
	display: flex;
    justify-content: center;
	align-items: center;
	margin-bottom: 20px;
	margin-top: 0px;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border-bottom: solid 5px rgb(144, 144, 149);
	color: #fff;
	font-size: 25px;
	font-family: 'vazir';
}
.formclass{
	display: flex;
	direction: rtl;
	align-items: center;
	flex-wrap: wrap;
}
.btnnewuser{
	color:#fff;
	background-color:#7cd867;
	outline: none;
    border: 0;
    color: #fff;
	padding:7px 20px;
	text-transform:uppercase;
	margin-top: 0px;
	margin-bottom:25px;
	border-radius:2px;
	cursor:pointer;
	position:relative;
	font-size: x-small;
	font-family: 'vazir';
}
.btnnewuser:hover{
	background-color:#4fd730;
}