.disp{
	position: fixed;
	width: 60%;
	min-width: 350px;
	top: 50px;
	background-color: #fff;
	box-shadow: 0px 0px 20px gray;
	height: 500px;
	overflow-y: auto;
}
.menubar{
    position: fixed;
    background-color: rgba(13, 110, 13, 0.416);
    top:10%;
    right: 5px;
    z-index: 1;
	border-radius:10px;
	margin-left: 0;
	padding: 5px;
	font-size: 0;
}

.icons{
	margin: 0;
	display: flex;
	justify-content: center;
	flex-direction: column;
	padding-top:5px;
	font-size: inherit;
}
.menubar:hover{
	font-size:12px;
	transition-property: all;
	transition-duration: 1s;
}


.toolbar{
	position: fixed;
	top:0;
	right: 0;
	width: 100%;
	height: 36px;
	background:linear-gradient(90deg,rgb(255, 255, 255) , rgb(87, 135, 190) 20%) ;
	display: flex;
	justify-content: space-between;
	box-shadow:5px 5px 10px 2px rgba(23, 12, 237, 0.2);
	
}
.homepage{
	color: #fff;
	font-size: 12px;
	font-family:vazir;
	margin: 5px;
}
.logoprof{
	display: flex;
	color: #fff;

}
.logo{
	margin-right: 40px;
}
.prof{
	margin-right: 5px;
	border-radius: 30px;
	margin-top: 3px;
}
.prof2{
	margin-right: 5px;
	border-radius: 30px;
	display: none;
}
.proflabel{
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: small;
	padding-bottom: 4px;
}
.prof:hover{
	cursor: pointer;
}
.prof:hover ~ .proflabel{
	color: black;
	cursor: pointer;
}
.proflabel:hover{
	color: black;
	cursor: pointer;
}
.homepages{
	display: flex;
	justify-content: left;
	color: #fff;
	font-size: small;
	direction: rtl;
	align-items: center;
}
.homepagex{
	margin-left: 10px;
	padding-bottom: 4px;
	background-color: #ffffff20;
	border-radius: 5px;
	padding: 5px;
}
.homepagex:hover{
	color: black;
	cursor: pointer;
	
}
@media(max-width: 600px){
	.menubar{
		position: fixed;
		background-color: rgba(13, 110, 13, 0.416);
		top:36px;
		right:0px;
		z-index: 1;
		border-radius:10px;
		margin-left: 0;
		font-size: 11px;
		padding: 5px;
		border-top-right-radius: 0px;
		border-bottom-right-radius: 0px;
	}
	.homepage{
		color: #fff;
		font-size: 9px;
		font-family:'vazir';
		margin: 3px;
	}
	.logoprof{
		display: flex;
		color: #fff;
	
	}
	.logo{
		margin-right: 10px;
	}
	.prof{
		margin-right: 5px;
		border-radius: 30px;
	}
	.prof2{
		margin-right: 5px;
		border-radius: 30px;
		display: inline;
	}
	.prof2:hover{
		border: solid 1px white;
	}
	.proflabel{
		display: flex;
		justify-content: center;
		align-items: center;
		font-size:9px;
		padding-bottom: 4px;
	}
	.homepages{
		display: flex;
		justify-content: left;
		color: #fff;
		font-size: 9px;
		direction: rtl;
		align-items: center;
	}
	.homepagex{
		margin-right: 4px;
		padding-bottom: 4px;
		font-size:9px;
		margin-left: 0px;
	}
	.homepage:hover{
		color: black;
		cursor: pointer;
		
	}

}

