.selectedOptionsList{
    margin: 2px;
    background-color: #6eac6e;
    min-width:45px;
    min-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    padding: 3px;

}
.slectedlist{
    display: flex;
    flex-wrap: wrap;
}

.slc{
    border-radius: 5px;
    background:transparent;
    padding:5px;
    font-family: 'vazir';
    font-size: 10px;
    color:#892020;	
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
    direction: ltr;
    border: 1px solid;
    border-radius: 2px;
    margin: 10px;
}

.slc:focus,slc:valid{
    border:none;	
	outline:none;
	border:solid 2px rgb(57, 73, 183);	
}

.slcrecivers{
    display: block;
    position: fixed;
    z-index: 100;
    width: 270px;
    height: 200px;
    background-color: rgba(182, 182, 182, 0.9);
    top:35%    ;
    right:35%;
    border-radius: 5px;
}
.searvhbar{
    font-size: x-small;
    display: block;
    background-color:#fff;
    border: 1px solid #333;
    border-radius: 2px;
    margin: 2%;
    width: 92%;
    direction: rtl;
    
}
.optreciver{
    height: 70%;
    overflow:auto;
}
.recivers{
        
    font-size: xx-small;
    display: block;
    background-color:#fff;
    border: 1px solid #333;
    border-radius: 2px;
    margin: 2%;
    width: 90%;
    
    direction: rtl;
    padding: 2px;
    cursor: pointer;
}
.slcbtn{
    display: flex;
    justify-content: center;
    
}
.sbtn{
    font-size: 10px;
    background-color: #6eac6e;
    min-width: 40px;
    min-height: 20px;
    border-radius: 5px;
    margin: 5px auto;
    border: none;
}
.sbtn:hover{
    cursor: pointer;
    background-color: #46ac46;
}