
.disp2{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.messagestype{
    display: flex;
    font-size:8px;
    margin: 5px;
    background-color:rgb(255, 255, 255) ;
    direction: rtl;
    align-items: center;
    min-height: 20px;
    border-radius: 2px;
    padding: 4px;
    margin-bottom:20px;
    cursor: pointer;
    

}
.listmesage{
    margin-bottom:5px;
    background-color :rgba(139, 143, 146,0.5) ;
    cursor: pointer;
}
.listmesage:hover{
    background-color: rgb(79, 80, 83);

}
.mesagetype{
    background-color:rgba(139, 143, 146,0.5);
    border-radius: 2px;
    height:15px ;
    margin:auto 4px ;
    padding:3px;
    
}
.mesagetype2{
   background-color :rgb(255, 255, 255);
   border-radius: 2px;
   height:15px ;
   margin:auto 4px ;
   padding:3px;
   cursor: pointer;
}
.mesagetype3{
    flex-grow: 1;
}
.mesagetype:hover{
    background-color: rgb(79, 80, 83);
    color: white;
    
}
