.input-container{
	position:relative;
	margin:20px 25px;
}
.input-container span{
	position:absolute;
	top:3px;
	right:5px;
	font-size:12px;
	color:#939393;	
	background-color:transparent;
	transition: all 0.5s ease-in-out;
}
.input-container input{ 
  border:solid 1px rgb(132, 142, 204);  
  border-radius: 5px;
  background:transparent;
  width:240px;
  padding:5px;
  font-size:13px;
  color:#892020;
}
.input-container input:focus{ 
	border:none;	
	outline:none;
	border:solid 1px rgb(39, 54, 151);	
   }

.input-container input:focus ~ span,
.input-container input:valid ~ span{
	color: rgb(12, 13, 13);
	top:-16px;
	font-size:11px;
	
}
