.Req{
    width: 100%;
    height: 100%;
    padding-right: 20px auto;
    padding-top: 15px;    
    margin: 0 auto;
    direction:rtl;
}
.buttuncuntiner{
    display: block;
    padding:10px auto;
    direction: ltr;
    margin: 10px;
}
.btnReq{
    padding: 8px 20px;
    color: #fff;
    background-color: rgb(125, 187, 120);
    margin: 10px ;
    border: 0;
    border-radius: 5px;
    font-size:small;
    font-family: 'vazir';
}
.btnReq:hover{
    background-color: rgb(39, 194, 28);
    color: black;

}
.editor{
    display: block;
    padding:10px auto;
    direction: ltr;
    border: 1px solid;
    border-radius: 2px;
    margin: 10px;
    min-height: 200px;
}

