.slt{
    border:solid 1px rgb(132, 142, 204);  
    border-radius: 5px;
    background:transparent;
    width:203px;
    padding:5px;
    color:#892020;
    font-family: 'vazir';
    font-size: 10px;
}
.slt:focus,slt:valid{
    border:none;	
	outline:none;
	border:solid 1px rgb(24, 40, 142); 
}