.toolbar{
	position: fixed;
	top:0;
	right: 0;
	width: 100%;
	height: 36px;
	background-color: #fff;
	background:linear-gradient(90deg,rgb(255, 255, 255) , rgba(81, 111, 209, 0.755) 20%) ;
	display: flex;
	justify-content: space-between;
	box-shadow:5px 5px 10px 2px rgba(23, 12, 237, 0.2);
	
}
.homepage{
	color: #fff;
	font-size: 12px;
	font-family: vazir;
	margin: 5px;
}



.text-centerrecpass{
	display:flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
}
.boxrecpass{
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #fff;
	box-shadow:5px 5px 10px 2px rgba(23, 12, 237, 0.2);
	border-radius: 10px;
	border:solid 1px rgb(113, 149, 188);
	display: flex;
	flex-direction: column;
}
.text-centerrecpass h1{
	background-color: rgb(93, 93, 236);
	display: flex;
    justify-content: center;
	align-items: center;
	margin-bottom: 20px;
	margin-top: 0px;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border-bottom: solid 5px rgb(144, 144, 149);
	height: 70px;
	
}
.boxlabelrecpass{
	font-size: medium;
	font-family: 'vazir';
	background-color: rgb(132, 132, 237);
	color: #fff;
	padding: 5px;
	border-radius: 8px;
}
.slctrecpass{
	display: flex;
	justify-content: baseline;
	margin:15px 25px;
	direction: rtl;
}
.slctrecpass span{
	font-size: 10px;
	margin-left:4px;
	margin-top:7px;
}
.btnrecpass{
	color:#fff;
	background-color:#eb651d;
	outline: none;
    border: 0;
    color: #fff;
	padding:7px 20px;
	text-transform:uppercase;
	margin-top: 0px;
	margin-bottom:25px;
	border-radius:2px;
	cursor:pointer;
	position:relative;
	font-size: x-small;
	font-family: 'vazir';
}
.btnrecpass:hover{
	background-color:#e7c83c;
}
.registerrecpass{
    color: #57ec5c;
    display: flex;
    font-size:10px;
    margin: 10px;
    justify-content: space-around;
	align-items:center ;
}
.linktorecpass{
    cursor: pointer;
    background-color: rgba(177, 175, 175, 0.2);
    padding: 5px;
    border-radius: 4px;
	text-decoration: none;
	font-size: 10px;
	font-family: 'vazir';
	
}
.linktorecpass:hover{
    background-color: rgb(177, 175, 175);
}
