.icon{
    padding: 3px;
    width: 25px;
    height: 30px;
    margin: 3px;
    background-repeat: no-repeat;
    background-size:25px 30px;
    background-position: center;
}
.iconinfo{
    height: 39px;
    padding: px;
    text-align: center;
    direction: rtl;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding-bottom: 3px;
    font-size: inherit;
    margin-left: 3px;
}

.linktoicons{
    background-color: transparent;
    margin-top: 4px;
    margin-left: 0px;
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction:row-reverse;
    text-decoration: none;
    font-family: 'vazir';
    color: black;
    cursor: pointer;
}
.linktoicons:hover{
    border: solid 1px rgb(47, 47, 161);
    color: green;
}