.slc{
    border-radius: 5px;
    background:transparent;
    padding:5px;
    font-family: 'vazir';
    font-size: 10px;
    color:#892020;	
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
    direction: ltr;
    border: 1px solid;
    border-radius: 2px;
    margin: 10px;
}

.slcin{
    border-radius: 5px;
    background:transparent;
    padding:5px;
    font-family: 'vazir';
    font-size: 10px;
    color:#892020;	
    display: flex;
    flex-wrap: wrap;
    direction: ltr;
    border: 1px solid black;
    border-radius: 2px;
    margin: 10px;
    margin-top: 0px;
    display: flex;
}
.editlabel{
    margin:5px 10px;
    font-size: medium;
    font-family: 'vazir';
    margin-left: 2px;
    margin-bottom: 0px;
}
.slcl{
    margin:5px 10px;
    font-size: medium;
    font-family: 'vazir';
    margin-left: 2px;
}
.slct{
    width: 80%;
    font-size: medium;
    font-family: 'vazir';
    margin:5px 10px;
    margin-right: 2px;
}
.Req{
    width: 100%;
    height: 100%;
    padding-right: 20px auto;
    padding-top: 15px;    
    margin: 0 auto;
    direction:rtl;
}
.buttuncuntiner{
    display: block;
    padding:10px auto;
    direction: ltr;
    margin: 10px;
}
.btnReq{
    padding: 8px 20px;
    color: #fff;
    background-color: rgb(125, 187, 120);
    margin: 10px ;
    border: 0;
    border-radius: 5px;
    font-size:small;
    font-family: 'vazir';
}
.btnReq:hover{
    background-color: rgb(39, 194, 28);
    color: black;

}
.editor{
    display: block;
    padding:10px auto;
    direction: ltr;
    border: 1px solid;
    border-radius: 2px;
    margin: 10px;
    min-height: 200px;
    margin-top: 2px;
}

