.contourbtn{
    display: flex;
    direction: ltr;
    padding: 5px 20px;
}
.btnGroups{
    padding: 8px 25px;
    margin: 5px;
    font-size: 12px;
    background-color: rgba(91, 126, 91, 0.715);
    border: none;
    border-radius: 5px;

}
.btnGroups:hover{
    background-color:rgba(31, 190, 31, 0.715);
    cursor: pointer;
}
.dispgroupsetting{
    position: fixed;
    top: 45px;
    right: 10%;
    min-width: 350px;
    height: 500px;
    width:80%;
    z-index: 3;
    background-color: rgba(12, 143, 209, 0.941);
    overflow-y: auto;
}
.contonerinput{
    margin: 5px;
    padding: 5px;
    font-size: 11px;
    font-family: 'vazir';
}
.contonerinput input{
    margin-right: 3px;
    font-family: 'vazir';
}
.beforcontourlist{
    display: flex;
    justify-content: center;
    margin: 0px;
    padding: 0px;
    background-color: transparent;
}
.contourlist{
    width: 95%;
    height: 300px;
    margin-top: 6px;
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    overflow-y: auto;
}
.contourlistchild{
    margin: 3px 10px;
    border-bottom: solid 1px rgba(21, 24, 26, 0.509);
    display: flex;
    justify-content: space-around;
}
.contourlistchild2{
    margin: 3px;
    background-color: rgba(0, 0, 0, 0.324);
    padding: 4px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: 14%;
    height: 15px;
    justify-content: center;
    font-size: 10px;
    overflow: hidden;
    max-width: 600px;
    
}
@media(max-width: 600px){
    .dispgroupsetting{
        position: fixed;
        top: 45px;
        right: 5%;
        min-width: 350px;
        height: 500px;
        width:80%;
        z-index: 3;
        background-color: rgba(12, 143, 209, 0.941);
        overflow-y: auto;
    }
    .contourlist{
        width: 85%;
        height: 300px;
        margin-top: 6px;
        padding: 5px;
        background-color: white;
        border-radius: 5px;
        overflow-y: auto;
    }
    .contourlistchild{
        margin: 3px 3px;
        border-bottom: solid 1px rgba(21, 24, 26, 0.509);
        display: flex;
        justify-content: space-around;
    }
    .contourlistchild2{

        margin: 3px;
        background-color: rgba(0, 0, 0, 0.324);
        padding: 2px;
        display: flex;
        align-items: center;
        border-radius: 5px;
        width: 20%;
        height: 15px;
        justify-content: center;
        font-size: 8px;
        overflow: hidden;
        max-width: 600px;
        
    }
    .contourlistchild3{
        width: 8%
    }
 

}